/* Estilo global */
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease; /* Adicione transição suave de cor de plano de fundo e cor do texto */
}

/* Modo Claro */
body:not(.dark-mode) {
  background-color: #f5f5f5; /* Cor de fundo clara */
  color: #cab3b3; /* Cor do texto escura */
}

/* Modo Escuro */
body.dark-mode {
  background-color: #1a1a1a; /* Cor de fundo escura */
  color: #ffffff; /* Cor do texto clara */
}

/* Cabeçalho */
.App-header {
  background-color: #4e5052;
  padding: 20px;
  text-align: center;
  color: #ffffff; /* Defina a cor desejada para o texto */
  font-size: 24px;
}

.blue-bg {
  background-color: #0077b6; /* Cor de fundo azul para o modo claro */
  color: #ffffff; /* Cor do texto branca */
}



.App-header h1 {
  margin: 0;
  color: #ffffff;
  font-size: 24px;
}

/* Botões */
.button {
  background-color: #fca311;
  color: #ffffff;
  border: none;
  padding: 12px 24px;
  margin-top: 16px;
  font-size: 18px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #e8590c;
}

.main-container {
  background-color: #333; /* Altere para a cor desejada */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}


/* Webcam */
.webcam-container {
  width: 100%;
  max-width: 600px;
  border: 1px solid #ccc;
  background-color: transparent;
  padding: 16px;
}

.webcam {
  width: 100%;
  height: auto;
}

/* Captured Image */
.captured-image-container {
  width: 100%;
  max-width: 600px;
  margin-top: 16px;
  text-align: center;
}

.captured-image {
  max-width: 100%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Geolocation */
.geolocation-container {
  margin-top: 16px;
}

.geolocation-text {
  font-size: 16px;
}

/* Date and Time */
.date-time-container {
  margin-top: 16px;
}

.date-time-text {
  font-size: 16px;
}

/* Comments */
.comments-container {
  width: 100%;
  max-width: 600px;
  margin-top: 16px;
}

.comments-input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Comment Submit Button */
.submit-button {
  background-color: #61dafb;
  color: #ffffff;
  border: none;
  padding: 12px 24px;
  margin-top: 16px;
  font-size: 18px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #45a049;
}

/* Botão Modo Claro/Modo Escuro */
.dark-mode-button {
  background-color: #333333;
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  margin-top: 16px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dark-mode-button:hover {
  background-color: #555555;
}


/* Adicione estas regras de estilo CSS para o container do mapa */
.google-maps-container {
  width: 100%;
  max-width: 600px; /* Largura máxima do container do mapa */
  height: auto; /* Ajusta a altura automaticamente à altura do conteúdo */
  margin-top: 8px; /* Reduza a margem superior para aproximá-lo da imagem capturada */
  margin-bottom: 8px; /* Adicione uma margem inferior para separação */
  text-align: center;
}

/* Estilo para o mapa embebido (se necessário) */
.google-maps-container iframe {
  width: 100%;
  height: 100%; /* O mapa ocupará toda a altura disponível */
  border: none; /* Remove a borda padrão do iframe, se houver */
}
